/* portfolio */
#portfolio{
    position:relative;
    width:100%;
    padding-top:4rem;
    box-sizing: border-box;
    display:table;
    text-align: center;
    margin:0 auto;
  }
  .box{
    width:80%;
    display: flex;
    padding-left:3rem;
    box-sizing: border-box;
    margin: 0 auto;
  }
  .card{
    position:relative;
    width:18.75rem;
    height:25rem;
    box-sizing: border-box;
    padding:1rem;
    box-shadow: 5px 11px 30px rgba(0,0,0,0.3);
    background-color: #fff;
    text-align: left;
    font-size:1rem;
    cursor: pointer;
    margin-right:1rem;
    margin-top:1rem;
  }
  .card:hover{
    margin-top:0.8rem;
  }
  p.img{
    width:100%;
    border: 1px solid #000;
    margin:0 auto 1rem;
    padding:1rem;
    box-sizing: border-box;
    overflow: hidden;
  }
  .spanText{
    font-weight:bold;
  }
  .detail{
    font-family: 'Gowun Batang', serif;
    font-size:0.88rem;
  }
  .more{
    position:absolute;
    bottom:3%;
    right:1rem;
  }
  
  /* tablet_size css */
  .wrapT{
    padding-left:0;
  }
  .cardT01:hover{
    left:22.3rem;
    }
    .cardT02:hover{
      left:42.3rem;
      }
    .cardT03:hover{
      top:35.2rem;  
      left:22.3rem;
      }
    .cardT01{
      left:22.5rem;
    }
    .cardT02{
      left:42.5rem;
    }
    .cardT03{
      top:35rem;
      left:22.5rem;
    }