/* article */
.artiOpen{
    position:fixed;
    top:0;
    bottom:0;
    left:0;
    z-index:999;
    padding:5rem 0.88rem 0;
    background-color: #f7e0bf;
    transition:0.3s ease-in-out;
  }
  .artiClose{
    position:fixed;
    top:0;
    bottom:0;
    left:-190px;
    z-index:999;
    padding:5rem 0.88rem 0;
    background-color: #f7e0bf;
    transition:0.3s ease-in-out;
  }
.intro{
      padding-top:1rem;
  }
.intro>img{
    width:0.7rem;
    margin-right:1rem;
}
.email>img{
    width:1.3rem;
}
.open{
    width:1.5rem;
    transition:0.1s ease-in-out;
    background-color: #f7e0bf;
    border:none;
    border-radius: 50%;
    padding:0.2rem 0.1rem 0 0.2rem;
    position:absolute;
    top:10%;
    left:95%;
    z-index: 1000;
    cursor: pointer;
}
.close{
    width:1.5rem;
    transition:0.1s ease-in-out;
    background-color: #f7e0bf;
    border:none;
    border-radius: 50%;
    padding:0.2rem 0.1rem 0 0.2rem;
    box-sizing: border-box;
    position:absolute;
    top:10%;
    left:95%;
    cursor: pointer;
}
.openImg{
    width:1rem;
    transform: rotate(180deg);
    transition:0.3s ease-in-out;
}
.closeImg{
    width:1rem;
    transform: rotate(0deg);
    transition:0.3s ease-in-out;
}