*{
  padding:0;
  margin:0;
}
ul,li,a{
  text-decoration: none;
  list-style: none;
  color:inherit;
}
img{
  width:100%;
}
h2>p{
  display:inline-block;
  border-bottom:5px solid #f7e0bf;
}
/* header */

header{
  width:100%;
  position:fixed;
  top:0;
  left:0;
  z-index: 1000;
  padding:0 0.88rem;
  /* background-color: #f7e0bf; */
}
h1{
  font-size:1.5rem;
  float:left;
  cursor: pointer;
}
.nav>ul{
  display:flex;
  padding-left:2rem;
}
.nav>ul>li{
  padding-left:3rem;
  padding-top:0.3rem;
}
/* navMobile css */
.navM>ul{
  display:flex;
  padding-left:3rem;
}
.navM>ul>li{
  padding-left:2rem;
  padding-top:0.3rem;
}

/* blog */
#blog{
  padding-top:3rem;
  padding-left:10rem;
  text-align: left;
}
#blog>h2>p{
  width:80%;
  box-sizing: border-box;
}
.blogBox{
  width:80%;
  padding-top:0.3rem;
}
.blogBox>li{
  border-bottom:1px solid #000;
  width:100%;
  position: relative;
  padding-top:1.5rem;
  padding-bottom:0.5rem;
  display:flex;
  text-align: center;
}
.date{
  padding-right:5rem;
}
