/* books */
#books{
  padding-top:5rem;
  text-align:left;
  padding-left:15%;
}
.booksNav{
  display:flex;
  vertical-align: middle;
  justify-items: center;
  padding-bottom:1rem;
}
.booksNav>li{
  padding-top:2rem;
  padding-left:2rem;
  text-align: center;
}
a:hover{
  width:100%;
  background-color: #f7e0bf;
}
.active{
  background-color: #f7e0bf;
  font-weight: bold;
}
.contants{
  display:flex;
}
.imgBox{
  width:15rem;
  height:15rem;
  border: 2px solid #949494;
  box-sizing: border-box;
  position:relative;
}
.main{
  width:60%;
  padding-top:1rem;
  margin: 0 auto;
  box-sizing: border-box;
}
.booksPic{
  position:absolute;
  bottom:-50%;
  width:100%;
  display:flex;
}
.booksPic>li{
  display: inline-block;
  width:15rem;
 text-align: center;
 border: 1px solid #000;
 padding:0.5rem;
 font-size:0.88rem;
 line-height: 2rem;
 color:#949494;
 cursor: pointer;
}
.booksPic>li:not(:last-child){
  margin-right:0.3rem;
}
.text{
  padding-left:3rem;
}
.text>p{
  font-size:0.9rem;
}
.dcBox{
  display: flex;
  position:relative;
  padding-top:1rem;
}
.dc{
  
  font-size:2.5rem;
  font-weight:bold;
  color:#fabb5c;
}
.won{
  padding-left:1rem;
}
.won>dt{
  text-decoration:line-through;
  color:#949494;
  display: inline-block;
  font-size: 0.88rem;
  line-height: 0.88rem;
}
.won>dd{
  font-size:1.2rem;
  line-height: 1.2rem;
  padding-top:0;
}
.numBox{
  display:flex;
  position:absolute;
  bottom:0.3rem;
  right:-2rem;
}
.num{
  border-radius: 50%;
  border:1px solid #000;
  width:1.5rem;
  height:1.5rem;
  font-size:1.2rem;
  text-align: center;
  cursor: pointer;
}
.point{
  width:3rem;
  height:1rem;
  border:1px solid #000;
  padding:0.3rem 0;
  text-align: center;
  line-height: 1rem;
  margin:0 0.5rem;
}
.service{
  width:100%;
  background: #ccc;
  padding:1rem;
  font-size:0.8rem;
}
.service>li{
  padding-bottom:1rem;
  padding-right:4rem;
}
.payBox{
  position: relative;
}
.paybtn{
  position: absolute;
  top:0.3rem;
  right:-2rem;
}
.All>img{
  padding-top:1rem;
  padding-bottom:1rem;
}
.allMain{
  width:80%;
  padding-top:3rem;
}