/* about */
#about{
    padding-top:5rem;
    display:table;
    margin:0 auto;
    padding-bottom:3rem;
  }
  #about>h2{
    margin:0 auto;
    text-align: center;
    padding:1rem 0;
  }
  /* pc_size css */
  .boxPc>dd{
    padding-left:5rem;
  }
  .boxPc>dd>ul>li{
    padding-bottom:1rem;
  }
  .boxPc{
    display:flex;
    padding-top:2rem;
  }
  dt.title{
    width:20rem;
    padding:1rem;
    box-shadow: 5px 11px 30px rgba(0,0,0,
  0.3);
  }
  /* tablet_size css */
  .boxT>dd>ul>li{
    padding-bottom:1rem;
  }
  .boxT{
    display:block;
    padding-top:2rem;
  }
  .textT{
      text-align: center;
  }
  /* Mobile_size css */
  .boxM>dd>ul>li{
    padding-bottom:0.5rem;
  }
  .boxM{
    display:block;
  }
  .textM{
      margin:0 auto;
      padding-top:1rem;
      font-size: 0.85rem;
      text-align: center;
  }
  .titleM{
    width:10rem;
    padding:1rem;
    box-shadow: 5px 11px 30px rgba(0,0,0,
  0.3);
  margin:0 auto;
  }